@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:rgb(155, 155, 155, 0.04);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color:  #282c34;
  --secondary-color:  #61dafb;
  --transparent-color: #ffffff00;
  --color: #80B525;
}

.main-container {
  min-height: 80vh;
}


button.btn-menu {
    background-color:  var(--transparent-color);
    border: none;
    height: auto;
    outline: none;
    width: 50px;
}

button.btn-menu .fa-times:before{
    color:  var(--color);
    height: 50px;
    vertical-align: middle;
    width: .8rem;
}

button.btn-menu i {
    color:  var(--color);
    font-weight: 900;
    font-size: 24px;
}

.header {
    background-color:  #fffffff0;
    padding: 19px 30px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}

.header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2rem;
}

.logos img{

    margin-left: -7px ;
    vertical-align: middle;
    width: 50px;
}

.menu {
    position: fixed;
    left: 0;
    top: 4.2rem;
    background-color: white;
    width: 100vw;
    display: flex;
    flex-direction: column;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.5s ease;
}

.menu.is-active {
    opacity: 1;
    pointer-events: auto;
}

.menu a {
    font-weight: bold;
    border-bottom: 1px solid var(--color);
    color: var(--color);
    font-size: 20px;
    margin: 0 30px; 
    padding: 15px 0;
    text-decoration: none;
}
.menu .a_whatsapp{
    border-bottom: none;
}
.menu a:last-child {
    border-bottom: none;
}

.menu a:hover {
    color: var(--secondary-color);
}

.none {
    display: none;
}
/* nav{
    color:black;
} */

.active-nav-background {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}
.fab{
    font-size: 35px;
}
.fa-facebook{
    color: blue;
}
.fa-whatsapp{
    color: seagreen;
}
@media screen and (min-width: 992px){

    button.btn-menu {
        display: none;
    }

    .menu {
        position: static;
        width: auto;
        flex-direction: row;
        opacity: 1;
        pointer-events: auto;
    }
    .menu a {
        text-transform: uppercase;
        border-bottom: none;
        padding: 0 1rem;
        font-size: 1.1rem;
    }

    .menu a:last-child {
        padding-right: 0;
    }

    .menu a:hover {
        background-color: transparent;
    }

    
}
.carousel-inner img{
    width: 100%;
    height: 500px;
}
.section{
    margin-top: 0;
    width: 100%;
    background-color: var(--color);
}
ul li{
    text-transform: uppercase;
    border-bottom: none;
    padding: 0 1rem;
    font-size: 1.1rem;
    font-weight: bold;
}
h1 .subrayado{
    color: var(--color);
}


.trato {
  font-size: 40px;
  font-weight: bold;
}

.correct {
  background-color: var(--color);
  border-radius: 8px;
  height: 30px;
  width: 30px;
}
.far {
  color: white;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.vertical-line-trato, .vertical-line-list{
    border-left: thick solid var(--color);
}
@media(max-width:768px){
  .list-group{
    margin: 30px 0 30px 0;
  }
  
}
@media(max-width:425px){
  .vertical-line-list{
    border-left: none;

    border-right: thick solid var(--color);
    
  }
  .list-group-item{
    font-size: 15px;
  }
}
.toCome{
    height: 290px;
    width: 100%;
    background-color: black;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
}

.img{
    object-fit: cover;
    height: 100%;
    width: 100%;
}
.card-img-overlay div span{
    font-weight: bold;
    color: white;
    font-size: 1.6rem;
}
.card-img-overlay div .btn{
    margin-top: 40px;
    background-color: var(--color);
    color: white;
    border-radius: 5px;
}
.location_title{
    font-size: 40px;
    font-weight: bold;
    margin: 20px 0 20px 0;
}
.location{
    height: 350px;
    width: 100%;
}
.a_reserva{
    text-decoration: none;
}
.a_reserva Button:after{
    border-color: red;
}
.footer_title{
    font-weight: bolder;
}
.footer_about,.footer_openHours,.footer_contact{
    text-align: center;
    margin-bottom: 0;
}
.footer_contactPhone{
    text-align: justify;
    padding-left: 5px;
}
.horizontal-line{
    box-shadow: 1px solid ;
    margin: 10px;
    color: var(--color);
}
.vertical-line-about, .vertical-line-openHours, .vertical-line-contact{
    border-left: thick solid var(--color);
}
.footer-copyright{
    font-weight: bold;
}
@media(max-width:768px){
    .footer_row{
      margin: 30px 0 30px 0;
    }
    .footer_row div{
        margin-bottom: 25px;
    }
    
    .footer_contactPhone{
        text-align: justify;
        padding-left: 30px;
    }
  }

  @media (max-width: 576px){
    .vertical-line-about, .vertical-line-openHours, .vertical-line-contact{
        border-left: none;
        border-bottom: 1px solid var(--color);
    }
  }
  @media(max-width: 425px){
      .footer-copyright{
          font-size: 13px;
      }
  }
